
.about-me-extended {
    display: grid;
    grid-template-columns: 1fr 2fr;
    background: #ebeef4;
    /* background: red; */
  
    width: 100vw;
    /* max-width: 100%; */
    height: 100vh;


  }

  .about-me-left {
    text-align: center;
    width: 100%;
    justify-self: center;
    place-items: center;
    display: grid;
  }
  .about-me-left-title {
    font-family: "Bebas Neue", cursive;
    font-size: 7vh;
    color: var(--color-accent);
  }
  .about-me-left-title2 {
    font-family: "Bebas Neue", cursive;
    font-size: 7vh;
    padding-top: 4rem;
    color: var(--color-accent);
    
  }
  .about-me-left-description {
    font-size: 3vh;
    margin: 0.5rem;
    font-family: "Alumni Sans Pinstripe", sans-serif;
    margin-bottom: 4rem;
    width: 45vw;
    animation: into-in-about 2s both;
    animation-delay: 1s;
  }
  
  @keyframes into-in-about {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 100;
    }
  }
  .about-me-left-desc {
    text-align: left;
    width: 45vw;
    margin: 1rem;
    align-self: center;
    /* border: 1px solid orange; */
  }
  .about-me-right {
    /* border: 1px solid black; */
    display: grid;
    place-self: center;
  }
  
  .name {
    color: var(--color-accent);
  }
  .about-me-left-name {
    font-size: 3vh;
    /* font-family: 'Bebas Neue', cursive; */
    text-align: left;
    margin-left: 1rem;
  }
  span {
    font-family: "Bebas Neue", cursive;
    /* color: var(--color-accent); */
  }
  .about-ext-img {
    height: 65vh;
    position: absolute;
    /* top: 90%; */
    top: 100vh;
    left: 10%;
    border-radius: 8%;
    box-shadow: inset 9.61px 9.61px 15px #cfd1d7,
      inset -9.61px -9.61px 15px #ffffff;
      animation: slide-in-image 1.5s both;
    /* z-index: 0; */
  }
  @keyframes slide-in-image {
    0% {
      transform: translateX(100vh);
    }
    100% {
      transform: 0;
    }
  }
  
  
  .about-ext-img2 {
    height: 65vh;
    position: absolute;
    top: 15%;
    left: 10%;
    border-radius: 8%;
    box-shadow: inset 9.61px 9.61px 15px #cfd1d7,
      inset -9.61px -9.61px 15px #ffffff;
    animation: slide-in-image 1.5s both;
    /* z-index: 0; */
  }
  
  .tech-stacks {
    /* background: #EEF0F4; */
    /* border-radius: 8px; */
    /* box-shadow: 5.11px 5.11px 15px #BBBCC0, -5.11px -5.11px 15px #FFFFFF; */
    height: 10vh;
    place-items: center;
    display: grid;
    color: #cbcbcb;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  }
  .container:hover {
    color: var(--color-accent);
  }
  
  .highlight-description {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 1rem;
    gap: 2vh;
    font-size: 2vh;
  }
  .highlight-title {
    color: var(--color-accent);
    font-weight: 900;
    font-family: "Bebas Neue", cursive;
  }

  .down-arrow-logo {
    color: var(--color-accent);
    /* color: white; */
    border-radius: 100%;
    box-shadow: 4.5px 4.5px 8px #bbbcc0, -4.5px -4.5px 8px #ffffff;
    cursor: pointer;
  }
  .down-arrow {
    position: absolute;
    color: #ffffff;
    padding-top: 2rem;
    right: 96vw;
    top: 162vh;
    background: none;
  }
  .up-arrow {
    position: fixed;
    color: #ffffff;
    padding-top: 2rem;
    right: 1vw;
    top: 83vh;
    background: none;
  }
  .up-arrow-logo {
    color: var(--color-accent);
    /* color: white; */
    border-radius: 100%;
    box-shadow: 4.5px 4.5px 8px #c0bebb, -4.5px -4.5px 8px #ffffff;
    cursor: pointer;
    left: 85%;
  }
  .stack {
    color: #e9820c;
    /* border-radius: 100%;
    box-shadow: 4.5px 4.5px 8px #BBBCC0, -4.5px -4.5px 8px #FFFFFF;
    cursor: pointer; */
    place-self: center;
    height: 6.5vh;
  }
  .container {
    height: 6.5vh;
    width: 6.5vh;
    border-radius: 100%;
    box-shadow: 4.5px 4.5px 8px #bbbcc0, -4.5px -4.5px 8px #ffffff;
    cursor: pointer;
  }
  
  .container:hover {
    animation: stack-in-bounce .5s both;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
  @keyframes stack-in-bounce {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-1.5vh);
  }
  }
  .tech-stacks-name {
    /* position: absolute; */
    /* top: 200%; */
    font-size: 3vh;
    /* color: #ebeef4; */
    /* color: blue; */
  }
  
  .html-container,
  .photoshop-container,
  .css-container,
  .javascript-container,
  .react-container,
  .firebase-container {
    display: grid;
    grid-template-rows: auto;
    justify-items: center;
  }
  .tech-stack-spacers {
    height: 0.5rem;
  }
  .photoshop-stack,
  .js-stack {
    margin-right: 1.5vw;
  }
  .instagram-link {
    text-decoration: none;
    color: var(--color-accent);
    cursor: pointer;
  }

  @media (max-width:600px) {
    .about-ext-img, .about-ext-img2 {
      display: none;
    }
    /* .about-me-left-title {
      place-self: center;
    } */
    .about-me-extended {
      width: 100vw;
      max-width: 100%;
      height: fit-content;
      padding-top: 10vh;
      padding: 1rem;
      font-size: 4vh;
    }
    .about-me-left-desc {
      width: 90%;
      place-self: center;
      font-size: 3.5vh;
    
  
    }
    .about-me-right {
      display: none;
    }
    .about-me-left-description {
      width: 85vw;
      margin: none;
      padding: none;
      font-size: 2vh;
    }
    .container {
      height: 4vh;
      width: 4vh;
    }
    .stack {
      height: 5vh;
    }
  }