@import url("https://fonts.googleapis.com/css2?family=Alumni+Sans+Pinstripe&family=Bebas+Neue&family=Chathura:wght@100;300&family=Cormorant:wght@300&family=Covered+By+Your+Grace&family=Italiana&family=Oxygen&family=Playfair+Display&family=Poppins:wght@800&family=Prosto+One&family=Roboto:wght@400;900&family=Syne:wght@800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  width: 1vw;
  max-width: 100%;
  /* background-color: red; */
  --color-accent:#FF8F32; 
  --background-color: #ebeef4;
}
.homepage {
  width: 99vw;
  max-width: 100vw;

}
.app {
  width: 80vw;
}
body {
  width: 100vw;
  max-width: 100%;
}
.link {
  color: black;
}
.nav {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  height: 10vh;
  position: fixed;
  width: 100vw;
  max-width: 100%;
  background: rgb(255, 254, 254);
  box-shadow: 0 4px 10px 0 #e3e2d9;
  -webkit-backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  font-family: "Cormorant", serif;
  z-index: 1;
}
/* .contact-btn {
  display: grid;
  grid-template-columns: 1fr 1fr;
} */

.name-logo {
  font-family: "Syne", sans-serif;
  justify-self: start;
  font-size: 2vh;
  align-self: center;
  padding-left: 1rem;
}

.home-btn,
.about-btn,
.contact-btn,
.portfolio-btn {
  /* border-radius: 4px; */
  height: 4vh;
  text-align: center;
  width: 6vw;
  transition: all 0.2s ease-in;
  
}

.home-btn:hover,.about-btn:hover,.portfolio-btn:hover,.contact-btn:hover {
  color: var(--color-accent);
  border-bottom: 1px solid var(--color-accent)
}

.name-logo-color {
  color: black;

}

.nav-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  list-style-type: none;
  gap: 2vw;
  font-size: 2vh;
  
}


@media (max-width: 600px) {
  :root {
    width: 100vw;
    max-width: 100%;
  }
  .app {
    width: 100vw;
    max-width: 100%;
  }
  .nav {
    display: none;
  }
}