/* PORTFOLIO */
.portfolio {
    /* height: 90vh; */
    background-color: #ffffff;
    /* background-color: black; */
    padding-top: 2.5rem;
    color: #3d6064;
    background: #ebeef4;
    width: 99vw;
  }
  .quick-description {
    text-align: center;
  }
  
  /* .tech-stacks {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    justify-items: center;
    padding-top: 3rem;
  } */
  .project-twitter,
  .project-weather,
  .project-shopping,
  .project-memory {
    display: grid;
    grid-template-columns: 5fr 0.2fr;
  }
  /* .project-twitter, .project-weather {
    justify-self: start;
  }
  .project-shopping, .project-memory {
    justify-self: end;
  } */
  .project-tech-stack-container {
    width: 45vw;
    display: flex;
    justify-content: space-evenly;
    background-color: rgba(20, 183, 183, 0.105);
  }
  
  .project-title {
    margin-top: 1rem;
    text-align: center;
    font-family: "Bebas Neue", cursive;
    color: #3d6064;
    text-shadow: -8px -8px 12px rgba(255, 255, 255, 0.4),
      8px 8px 12px rgba(0, 0, 0, 0.08);
    caret-color: #262626;
    outline: none;
  }
  .project-image {
    height: 40vh;
    width: 90%;
    margin: 1rem;
  }
  .image-overlay {
    display: grid;
    grid-template-columns: 2fr 1fr;
    position: absolute;
    height: 50vh;
    width: 90vw;
    opacity: 0;
    gap: 2vw;
  }
  .hover-description {
    margin: 1rem;
    display: grid;
    grid-template-rows: 0.5fr 2fr 0.5fr;
  }
  .image-overlay:hover {
    opacity: 1;
    /* background: rgba( 255, 255, 255, 0.6 );
    box-shadow: 0 8px 32px 0 rgba( 61, 96, 100);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 ); */
    background: linear-gradient(145deg, #c8cacf, #ffffff);
    border-radius: 10px;
    box-shadow: 9.61px 9.61px 15px #cfd1d7, -9.61px -9.61px 15px #ffffff;
  }
  .image-overlay-reverse {
    display: grid;
    grid-template-columns: 1fr 2fr;
    position: absolute;
    height: 50vh;
    width: 90vw;
    opacity: 0;
  }
  .image-overlay-reverse:hover {
    opacity: 1;
    background: rgba(255, 255, 255, 0.6);
    box-shadow: 0 8px 32px 0 rgba(61, 96, 100);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
  }
  .project-container {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    justify-self: center;
    width: 95vw;
    margin: 1rem;
    gap: 1rem;
  }
  .hover-description-title {
    text-align: center;
    margin: 1rem;
    color: #3d6064;
  }
  .hover-description-text {
    font-family: "Alumni Sans Pinstripe", sans-serif;
    font-size: 2.5vh;
    font-weight: 700;
  }
  .view-project-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 2vh;
    color: #d7983e;
    width: 100%;
    place-self: center;
    
  }
  .project-names {
    display: grid;
    font-size: 20vh;
    text-transform: capitalize;
    font-family: "Alumni Sans Pinstripe", sans-serif;
    letter-spacing: 12px;
    color: #f16731a3;
    border-radius: 5px;
    box-shadow: inset 9.61px 9.61px 15px #cfd1d7,
      inset -9.61px -9.61px 15px #ffffff;
    width: 90vw;
    max-width: 100vw;
    text-align: center;
    height: 25vh;
    animation: project-title ease-in 1.5s both ;
  }
  @keyframes project-title {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 100;
    }
  }
  
  .project-details {
    height: 45vh;
    width: 95vw;
    font-family: "Syne", sans-serif;
    font-size: 1.5vh;
    display: grid;
    place-items: center;
    cursor: pointer;
    /* background: rgb(255, 255, 255);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.072);
    backdrop-filter: blur( 5px );
    -webkit-backdrop-filter: blur( 5px );
    border-radius: 10px; */
  }
  
  .project-description-title {
    font-size: 3vh;
    text-align: center;
  }
  .project-description {
    font-size: 2vh;
    margin: 1rem;
  }

  .container:hover {
    animation: stack-in-bounce .5s both;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
  @keyframes stack-in-bounce {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-1.5vh);
  }
  }
.view-project-container{
  width: 100%;
  border: 1px solid black;
  justify-items: center;
}
  .view-proj{
      height: 6.5vh;
      width: 6.5vh;
      border-radius: 100%;
      box-shadow: 4.5px 4.5px 8px #bbbcc0, -4.5px -4.5px 8px #ffffff;
      cursor: pointer;
      justify-self: center;
    
  }
  