.about {
    display: grid;
    /* grid-template-columns: 2fr 1fr; */
    /* background-color: rgba(250, 235, 215, 0.643); */
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    /* background-color: rgb(24, 23, 23);
    color: white; */
    /* background: #ebeef4; */
    background-color: red;
    padding-bottom: 20vh;
  }
  

  .show-about-me {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5vh; 
    text-transform: uppercase;
    padding-left: 0.2rem;
    color: var(--color-accent);
  }
  .about-me-arrow {
    display: grid;
    grid-template-columns: 1fr 0.5fr;
    place-items: center;
  }
  
  
  .left-side-about {
    display: grid;
    grid-template-rows: 1fr 0.1fr 3fr;
    margin: 3rem;
    padding-top: 4rem;
    /* width: 60%; */
  }
  .right-side-about {
    display: grid;
    align-items: end;
  }
  
  .about-details {
    font-size: 3vh;
    font-family: "Alumni Sans Pinstripe", sans-serif;
    width: 60%;
    animation: slide-in-bottom 1.5s both;
    /* z-index: 1; */
  }
  
  @keyframes slide-in-bottom {
    0% {
      transform: translateY(100vh);
    }
    100% {
      transform: 0;
    }
  }
  .about-details2 {
    font-size: 3vh;
    font-family: "Alumni Sans Pinstripe", sans-serif;
    width: 50%;
  }
  .down-arrow-about {
    /* color: var(--color-accent); */
    color: var(--color-accent);
    border-radius: 100%;
    box-shadow: 4.5px 4.5px 8px #bbbcc0, -4.5px -4.5px 8px #ffffff;
    cursor: pointer;
    animation: slide-in-bounce 2s both;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    z-index: 1;
  }
  @keyframes slide-in-bounce {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-1.5vh);
    }
  }
  .spacers {
    height: 30vh;
  }
  .background-letter {
    /* font-family: "Bebas Neue", cursive; */
    font-family: 'Poppins', sans-serif;
    position: absolute;
    z-index: 0;
    /* left: 42%; */
    top: 56%;
    left: 13%;
    /* rotate: 90deg ; */
    opacity: 20%;
    font-size: 27vh;
    color: var(--background-color);
    text-shadow: 9.91px 9.91px 15px #D9DADE, -9.91px -9.91px 15px #FFFFFF;
    animation: background-letter 2s both;
    animation-delay: 2s;
    background: none;

  }


@keyframes background-letter {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 50;
  }
  100% {
    opacity: 100;
  }
}

.background-letter2 {
  /* font-family: "Bebas Neue", cursive; */
  font-family: 'Poppins', sans-serif;
  position: absolute;
  z-index: 0;
  left: 13%;
  top: 56%;
  /* rotate: 90deg ; */
  opacity: 20%;
  font-size: 27vh;
  color: var(--background-color);
  text-shadow: 9.91px 9.91px 15px #D9DADE, -9.91px -9.91px 15px #FFFFFF;
  animation: background-letter2 1.5s both;
  animation-delay: 1.7s;
  background: none;

}
@keyframes background-letter2 {
  0% {
    opacity: 100;
  }
  100% {
    opacity: 0;
  }
}
.blink {
  font-family: 'Poppins', sans-serif;
  animation: blink 1.5s both;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100;
  }
}
  .about-name {
    /* z-index: 1; */
    /* font-family: "Syne", sans-serif; */
    /* color: var(--color-accent); */
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    transition: color 1s ease-in-out 0.25s;
    animation: slide-in-left 1.5s both;
    font-size: 8vh;
    /* animation-iteration-count: infinite; */
    /* color: var(--background-color);
    text-shadow: 9.91px 9.91px 15px #D9DADE, -9.91px -9.91px 15px #FFFFFF; */
    /* color: var(--background-color); */
    /* text-shadow: 9.91px 9.91px 15px #D9DADE, -9.91px -9.91px 15px #FFFFFF; */
  }
  
  @keyframes slide-in-left {
    0% {
      transform: translateX(-50vw);

    }
    100% {
      transform: 0;
      color: var(--color-accent);
    }
  }
  
  
  .about-name:hover {
    color: var(--color-accent);
  }
  .about-job {
    font-size: 2.5vh;
    font-family: "Syne", sans-serif;
    color: var(--color-accent);
    animation: slide-in-right 1.5s both;
  }
  
  @keyframes slide-in-right {
    0% {
      transform: translateX(20vw);
    }
    100% {
      transform: 0;
    }
  }

  .line-through {
    font-size: 10vh;
    position: absolute;
    top: 82%;
    left: 30%;
    animation: line-through 1.5s both;
    animation-iteration-count: 2;
    animation-direction: alternate;
  }
  @keyframes line-through {
    0% {
      transform: translatex(130vh);
      opacity: 0;
    }
    100% {
      transform: translatex(-25vh);
      opacity: 100;
    }
  }

  @media (max-width: 600px) {
    :root {
      padding: none;
      border: none;
      flex-wrap: wrap;
      width: 100vw;
      max-width: 100%;
      height: auto;
    }
    * {
      /* border: 1px solid black; */
      margin: 0;
      padding: 0;
      /* overflow: auto; */
    }

    .down-arrow, .up-arrow, .about-me-right {
      display: none;
    }
    .about-me-right {
      display: none;
    }
    .left-side-about {
      width: 100%; 
      height: 60vh;
      max-height: 100%;
    }
    .about {
      display: grid;
      grid-template-rows: 1fr 1fr;
      width: 100vw;
      height: 100vh;
      max-height: 100%;
      border: 1px solid black;
    }

    .about-name {
        width: 80vw;
        margin: none;
        padding: none;
        font-size: 5vh;
        height: 100vh;
        text-align: center;

    }
    .about-details {
      /* width: 70vw;
      max-width: 100%;
      height: 100vh;
      max-height: 100%;
      justify-self: start;
      font-size: 5vh; */
      display: none;
    }
    .background-letter, .background-letter2{
      font-size: 8vh;
      width: 100vw;
      max-width: 80%;
      /* position: absolute; */
      top: 35%;
      place-self: center;
    }
    .line-through {
      top: 45%;
    }
    .homepage {
      display: grid;
      grid-template-rows: auto;
    }

  }