/* Twitter twitter twitter twitter */
.individual-project-container {
    display: grid;
    grid-template-rows: 1fr 1fr;
    font-family: "Alumni Sans Pinstripe", sans-serif;
  }
  .individual-project-show-container {
    margin: 1rem;
    display: grid;
    grid-template-columns: 2fr 1fr;
    width: 100vw;
    animation: video-popup 2s both;
    animation-delay: 1s;
  }








@keyframes video-popup {
0% {
opacity: 0;
}
75% {
opacity: 50;
}
100% {
opacity: 100;
}
}



  .video-container {
    /* background-color: black; */
    height: 50vh;
    width: 50vw;
    margin: 3rem;
    color: white;
    display: grid;
    /* place-self: center; */
    border-radius: 8px;
    box-shadow: inset 9.61px 9.61px 15px #cfd1d7,
      inset -9.61px -9.61px 15px #e6e7ebef;
  }
  .video-player {
    place-self: center;
    height: 45vh;
    width: 50vw;
    border: none;
    /* position: absolute; */
    opacity: 1;
  }
  /* .video-player:hover {
    opacity: 1;
  } */
  .individual-projects-hover-title {
    font-size: 2vh;
    color: var(--color-accent);
    text-transform: uppercase;
    text-align: center;
    /* text-shadow: 9.91px 9.91px 15px #c8cbd3, -9.91px -9.91px 15px #ffffff;; */
    font-family: "Poppins", sans-serif;
    border: none;
    background: none;
    margin-bottom: 1rem;
    cursor: pointer;
    text-decoration: none;
    background: #EEF0F4;
    box-shadow: 2px 2.61px 2px #c8cacd, -2.61px -1.61px 5px #ffffff;
    border-radius: 3px;
    width: 75%
    /* font-family: 'Covered By Your Grace', cursive; */
  }
  .individual-project-details {
    /* background-color: gray; */
    /* height: 50vh; */
    width: 100vw;
    margin-top: 23vh;
  }
  .individual-project-details {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  /* .individual-image-container {
    background-color: white;
    display: grid;
    grid-template-columns: 1fr;
  } */
  
  .individual-project {
    display: grid;
    grid-template-rows: 1fr 1fr;
  }
  .individual-project-title-desc-container {
    place-self: center;
    margin: 1.5rem;
  }
  .individual-project-show-desc {
    padding-top: 7rem;
    font-size: 2.5vh;
    margin-right: 3rem;
  }
  .individual-project-title {
    text-align: center;
    text-transform: capitalize;
    /* font-family: 'Poppins',sans-serif; */
    color: var(--color-accent);
    text-transform: uppercase;
    font-size: 4vh;
    animation: image-popup 1.5s both;
    animation-delay: 1s;
  }
  .individual-project-description {
    font-size: 2.5vh;
    margin: 1rem;
    animation: image-popup 1.5s both;
    animation-delay: 2s;
  }
  /* .individual-project-show-desc-container {
  width: 10vw;
  } */
  .individual-img {
    width: 100%;
    height: 50vh;
    width: 50vw;
    /* animation: image-popup 1.5s both;
    animation-delay: 2s; */
    animation: image-popup 2s both;
    animation-delay: 1s;
  }



@keyframes image-popup {
0% {
  opacity: 0;
}
75% {
  opacity: 50;
}
100% {
  opacity: 100;
}
}

  
  .individual-content-container {
    height: 70vh;
    display: grid;
    grid-template-columns: 2fr 3fr;
    margin: 1rem;
  }
  .individual-image-containers {
    place-self: center;
    overflow: scroll;
    display: grid;
    margin: 1rem;
    height: 50vh;
    width: 50vw;
    border-radius: 8px;

  }




  .images-container {
    box-shadow: inset 9.61px 9.61px 15px #cfd1d7,
      inset -9.61px -9.61px 15px #ffffff;
    height: 55vh;
    width: 55vw;
    place-self: center;
    border-radius: 8px;
    animation: image-container-slide 1.5s both;
  }

@keyframes image-container-slide {
  0% {
    transform: translateY(100vh);
  }
  100% {
    transform: 0;
  }
}



  .individual-image-containers::-webkit-scrollbar {
    width: 0.01em;
  }
  

  
  .individual-image-containers::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
  }
  
  /* .img-container {
    display: grid;
    grid-template-columns: repeat(auto, 1fr);
  
  } */
  .individual-video-code-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    animation: image-popup 1.5s both;
    animation-delay: 2s;
  }
  
  .orange{
    color: orange;
  }
